import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLoign: 0,
    products: [
      { name: 'apple', price: '2' },
      { name: 'banana', price: '3' },
      { name: 'pear', price: '4' },
      { name: 'melon', price: '5' },
    ]
  },
  //必须都是同步操作
  mutations: {
    setIsLogin(state) {
      state.isLoign = 1;
      // Vue.prototype.bus.$emit("isLogin");
    }
  },
  //可以进行异步操作
  actions: {
    setIsLogin({ commit }) {
      setTimeout(()=>{
        commit("setIsLogin")
      }, 2000)
    }

  },
  modules: {

  },
  getters: {
    isLoign: (state) => {
      return state.isLoign;
    }
  }
})
