<template>
  <div class>
    <div @click="onLogin">登录</div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {};
  },
  computed: {
    isLogin() {
      return this.$store.getters.isLoign;
    }
  },
  watch: {
    isLogin() {
      console.log("isLogin 发生改变");
      
      // this.$router.go(-1); //登录成功后返回上一页
      this.$router.push("/"); //登录成功后重定向
    }
  },
  props: {},
  started() {
    //进行数据的获取，此时已经可以请求this了
    //如果需要操作domo，则需要在monted中
  },
  components: {},
  mounted() {
    //登录成功
    this.bus.$on("isLogin", () => {
      // this.$router.go(-1); //登录成功后返回上一页
      // this.$router.push("/"); //登录成功后重定向
    });
  },
  methods: {
    onLogin() {
    //$store.getters 和 $store.state获取的数据不一致，必须从getters中才可以获取到
      this.$store.dispatch("setIsLogin");
    }
  }
};
</script>

