<template>
  <!-- 页面刷新的办法 -->
  <div class="about">
    <h1>This is an about page</h1>
    <router-link to="/adetail">adetail</router-link>
    <!-- 父子组件传值 可以传静态和动态 除了字符串剩下的都得用v-bind通知Vue -->
    <!-- 父子组件通信，子组件emit，父组件on；子组件直接找到父组件然后修改，this.$parent.$data-->

    <!-- 插槽，如果子组件的template中没有包含一个slot，则该组件标签的内容都会被抛弃 -->
    <!-- 具名插槽，内容可以放置于子组件的对应位置 -->
    <aDetail name="detail" @changeName="changeName" :obj="obj">
      <template v-slot:header>
        <p>这是header</p>
      </template>
      <div>main</div>
      <template v-slot:footer>
        <p>这是footer</p>
      </template>
    </aDetail>
    <!-- <router-view/> -->

    <div @click="onRefresh">刷新当前页面</div>
  </div>
</template>


<script>
import aDetail from "./aDetail";
export default {
  name: "",
  data() {
    return {
      obj: {
        a: 1,
        b: 2
      }
    };
  },
  props: {},
  components: {
    aDetail
  },
  computed: {
    
  },
  //子组件引用 provide 注册的方法
  inject:['reload'],
  mounted() {
    console.log("重载")
  },
  methods: {
    changeName() {
      console.log("父节点接收到了改变的消息");
    },
    //刷新页面
    onRefresh() {
      //法一
      // location.reload();
      //法二
      // this.$router.go(0);
      //法三 不会有白屏，但是点击浏览器的返回按钮没有反应，因为跳转的是refresh页面，又会跳转回来
      // this.$router.push({path:'/refresh',query:{path:this.$route.fullPath}})
      //法四 使用provide / inject 组合，是最优解，利用v-if刷新页面的原理
      this.reload();
    }
  }
};
</script>

