import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/home.vue'
import Login from "../views/login.vue"
import Adetail from "../views/aDetail.vue"
import refresh from "../views/refresh"
import about from "../views/about"
import a404 from "../views/404"

Vue.use(VueRouter)
//路由，父子嵌套的话，得有对应的router-view来展示内容
const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
    //懒加载方式，只有到了该页面的时候才会加载相关资源
    // component: resolve => require(['./page/linkParamsQuestion.vue'], resolve)
  },
  {
    path: "/login",
    name: "Login",
    component: Login
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: about,
    children: [
      {
        path: "/adetail",
        name: "Adetail",
        component: Adetail
      }
    ]
  },
  {
    path: "/refresh",
    name: "refresh",
    component: refresh
  },
  //对于任何找不到的路由，统一返回404页面
  {
    path: "*",
    component: a404
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  // scrollBehavior(to, from, savedPosition){
  //   console.log(to, from, savedPosition)
  //   return{x: 0, y: 0} //路由新页面滚动到顶部，有锚点可以跳转
  // }
})

export default router
